.main {
    background-color: #E5E5E5;
    height: max-content;
}

.main .header {
    background-color: #D5D4DB;
    display: flex;
    justify-content: space-around;
    padding-left: 10px;
    padding-right: 10px;
}

.tabs {
    min-height: 50px;
    min-width: 60px;
    background: none;
    border: 0 solid #ffffff;

    font-size: 14px;
    color: #777777;
    cursor: pointer;
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

